.card {
    width: 190px;
    height: 180px;
    margin: 2px;
    /* background-color: rgba(237, 240, 237, 0.977); */
    color: rgb(14, 13, 13);
    text-align: center;
    border: 2px solid black;
    border-radius: 20px;
    box-shadow: inset 5px 5px 10px #bbb, inset -5px -5px 10px #fff;
    transition: 0.5s;
  }
  
  .card:hover {
    height: 125px;
  }
  
  .icon {
    width: 70px;
    height: 50px;
 
    font-size: 30px;
    background-color:  rgba(238, 238, 238, 0.977);
    margin: 20px auto;
    padding: 5px;
    border-radius: 10%;
    box-shadow: 8px 8px 10px #ddd, -8px -8px 10px #fff;
    align-items: center;
    align-content: center;
  }
  
  .content {
    color: rgb(14, 13, 13);
    background-color: #eee;
    padding: 1px;
    margin: 5px 10px;
    border-radius: 8px;
    box-shadow: 8px 8px 10px #ddd, -8px -8px 10px #fff;
    transform: translateY(-80px) scale(0);
    transition: all 0.5s;
  }
  
  .card:hover .content {
    transform: translateY(0) scale(1)
  }
  
  .content h3 {
    text-shadow: 2px 2px 0px #fff;
  }
 
  .img{
    width: 70px;
    height: 50px;
 }
 
 .buttonX {
   background: #8BF5FA;
   padding: 5px 1rem;
   margin-top: 15px;
   border: 0;
   color: black;
   font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
   font-size: 1rem;
   border-radius: 100rem;
   transition: 0.2s;
   font-weight: 600;
 }
 
 .buttonX:hover {
   color: rgb(78, 78, 78);
   outline-offset: 4.5px;
   background: red;
 }
 
